/*--------------------------------------------------------------
# Card--with-data - These cards contain tabs
--------------------------------------------------------------*/
.card--with-data {
  .card {
    &__title {
      margin-bottom: rem(28);
    }

    &__panel {
      display: flex;

      &[aria-hidden="true"] {
        display: none;
      }
    }

    &__actions {
      text-align: center;

      .btn {
        width: 100%;
        max-width: rem(280);
      }
    }
  }

  .tabs {
    &__list {
      padding-bottom: rem(40);
      margin-bottom: rem(24);
      border-bottom: var(--brdr-xxs) solid var(--charcoal-light);
      display: flex;
      justify-content: space-between;
    }

    &__item {
      flex-basis: 48%;
      flex-grow: 0;
    }

    &__section {
      margin-bottom: rem(28);
      margin-top: 0;
      padding: 0;
      background-color: transparent;
    }

    &__body {
      display: flex;
    }
  }

  .btn--toggle {
    width: 100%;
    padding: rem(16) rem(16);
    text-align: center;
  }

  .stat {
    border-right: var(--brdr-xxs) solid var(--charcoal-light);

    &__heading {
      display: none;
    }

    &:last-child {
      border: none;
    }

    &--datapoint {
      padding: rem(16) 0;
    }
  }
}
