.facetwp {
  &__search-bar {
    //@include row;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    margin: rem(32) auto;
    padding-top: rem(32);
    border-top: var(--brdr-xxs) solid var(--color-base-darker);

    .results {
      padding-left: rem(32);
      &:before {
        content: "";
        display: inline-block;
        border-left: var(--brdr-xxs) solid var(--color-base-darker);
        padding-left: rem(32);
        height: 2rem;
        vertical-align: middle;
      }
    }
    .filters-view {
      margin-left: auto;
    }
  }

  &-facet {
    &.facetwp-type-pager {
      margin: rem(32) auto;
      text-align: center;
    }
  }
}
.facetwp-radio,
.facetwp-checkbox {
  &.disabled {
    opacity: 1 !important;
    cursor: default;
    background: none;
    margin-bottom: 0;
    padding-left: 0;
    height: 0;
  }
}
.facetwp-slider-reset {
  display: none !important;
}
