/*--------------------------------------------------------------
# Global styles
--------------------------------------------------------------*/
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  overflow-x: hidden; }

a:focus, a:active,
button:focus,
button:active,
input:focus,
input:active,
select:focus,
select:active,
textarea:focus,
textarea:active,
[role="tabpanel"]:focus,
[role="tabpanel"]:active,
.a11y-modal:focus,
