  padding: 0; }

svg {
  overflow: visible; }

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important; }

.d-none {
  display: none; }

.hide-mobile {
  display: none; }

.hide-desktop {
  display: block; }

@media only screen and (min-width: 63.9375em) {
  .hide-mobile {
