/*--------------------------------------------------------------
# Type Scale
    > Variables for fonts
    > Variable for base font-size
    > Variable for scaling ratio
    > Type scale based on font-size and scaling ratio
    > Variables for line-heights
    > Breakpoints to adjust typesetting
--------------------------------------------------------------*/
@font-face {
  font-family: 'objectsans-md';
  src: url("../fonts/PPObjectSans-Medium.woff2") format("woff2"), url("../fonts/PPObjectSans-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'objectsans';
  src: url("../fonts/PPObjectSans-Regular.woff2") format("woff2"), url("../fonts/PPObjectSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'machina';
  src: url("../fonts/PPNeueMachina-Medium.woff2") format("woff2"), url("../fonts/PPNeueMachina-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

:root {
  --font-primary: objectsans, sans-serif;
  --font-secondary: objectsans-md, sans-serif;
  --font-tertiary: machina, sans-serif;
  --text-base-size: 1.125rem;
  --text-scale-ratio: 1.25;
  --text-xs: calc((var(--text-base-size) / var(--text-scale-ratio)) / var(--text-scale-ratio));
  --text-sm: calc(var(--text-xs) * var(--text-scale-ratio));
  --text-md: calc(var(--text-sm) * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-lg: calc(var(--text-md) * var(--text-scale-ratio));
  --text-xl: calc(var(--text-lg) * var(--text-scale-ratio));
  --text-xxl: calc(var(--text-xl) * var(--text-scale-ratio));
  --text-xxxl: calc(var(--text-xxl) * var(--text-scale-ratio));
  --text-4xl: calc(var(--text-xxxl) * var(--text-scale-ratio));
  --heading-line-height-lg: calc(2px + 2ex + 2px);
  --heading-line-height-sm: calc(0.5px + 2ex + 0.5px);
  --base-line-height: 1.5; }

/*--------------------------------------------------------------
Increase font-size @ specified breakpoints
--------------------------------------------------------------*/
@media only screen and (min-width: 56.25em) {
  :root {
    --text-base-size: 1.1875rem;
    --text-scale-ratio: 1.25; } }

@media only screen and (min-width: 75em) {
  :root {
    --text-scale-ratio: 1.333; } }

