/*--------------------------------------------------------------
# Modal - Default styles
--------------------------------------------------------------*/
/**
 * Including the at-only style with the dialog CSS as it is required
 * to visually hide a dialog's heading if desired.
 */
.at-only:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  height: 0.0625rem;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 0.0625rem; }

/**
 * Trigger styling
 */
[data-modal-open][hidden] {
  display: none; }

/**
 * When a modal dialog is open, don't allow scrolling of content
 * beneath (on desktop). Also useful in negating instances of
 * double scroll bars.
 * (browser window + dialog if long enough content)
 */
body.modal-open {
  overflow: hidden; }

/**
 * Modal Dialog base styling
 */
.js [data-modal],
.a11y-modal {
  -webkit-overflow-scrolling: touch;
  background: white;
  bottom: 0;
  margin: 0;
  max-height: 100%;
  height: 100vh;
  width: 37rem;
  opacity: 1;
  overflow: auto;
  padding: 1.5rem;
  position: fixed;
  right: 0;
  top: 0;
  visibility: visible;
  width: 100%;
  z-index: 100;
  overflow-y: auto; }

.js [data-modal],
.a11y-modal {
  transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out; }

/**
 * To allow for CSS animations, hidden modal dialogs are
 * not set to display: none, but rather the following rule
 * set, in combination with the dialog's default
 * position: fixed, will keep dialogs hidden from
 * all users until opened.
 *
 * This also solves an issue with iOS VO + Safari not allowing
 * modal dialogs to be focused, if the dialog is initially
 * set to "display: none".
 */
.js [data-modal][hidden],
.a11y-modal[hidden] {
  display: block;
  opacity: 0;
  pointer-events: none;
  transform: translateX(40vh);
  visibility: hidden; }

.a11y-modal__close-btn {
  border: none; }

/* Default close button hidden by default */
.is-icon-btn {
  background: none;
  border: none;
  height: 2em;
  padding: 0;
  position: absolute;
  right: 1em;
  top: 1em;
  width: 2em;
  display: none; }

.is-icon-btn:hover [data-modal-x],
.is-icon-btn:focus [data-modal-x] {
  color: #600;
  transform: scale(1.3) rotate(45deg);
  transition: transform 0.2s ease-in-out; }

[data-modal-x] {
  border-right: var(--brdr-xs) solid;
  bottom: 0;
  display: block;
  height: 2em;
  left: 0;
  margin: auto;
  pointer-events: none;
  position: relative;
  right: 0;
  top: 0;
  transform: rotate(45deg);
  width: 0.125rem; }

[data-modal-x]:after {
  border-right: var(--brdr-xs) solid;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transform: rotate(-90deg);
  width: 100%; }

